import {Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation} from '@angular/core'
import {Router} from '@angular/router'

@Component({
  selector: 'app-page-header',
  templateUrl: 'page-header.html',
  styleUrls: ['page-header.less'],
  encapsulation: ViewEncapsulation.None
})

export class PageHeaderComponent implements OnInit {
  @Input() title?: string
  @Input() rightButtons?: any  // header 
  @Input() leftButtons?: any // header
  @Input() permission?: any = 1 // 
  @Output() tapRightBtn = new EventEmitter()// listitem

  constructor(public router: Router) {
  }

  ngOnInit(): void {
  }

  clickRightBtn(action: string) {
    this.tapRightBtn.emit(action)
  }
}
