import {Injectable} from '@angular/core'
import {FileUploader} from 'ng2-file-upload'
import {ConstantService} from './constant.service'
import appAlert from '../../utils/alert'

/**
 * @desc 
 * @memberOf Services
 */
@Injectable()
export class UploaderService {

  public static MAX_FILE_SIZE = 5 * 1024 * 1024
  static formatBytes(bytes, decimals?) {
    if (bytes === 0) { return '0 Bytes'}
    const k = 1024,
      dm = decimals || 2,
      sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
      i = Math.floor(Math.log(bytes) / Math.log(k))
    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i]
  }

  constructor() {
  }

  /**
   * @desc FileUploader
   * @return FileUploader
   * @memberOf Services.UploaderService
   */
  public getFileUploader() {
    const token = window.localStorage.getItem('feathers-jwt')
    // B: uploader,inputuploader
    let uploader: FileUploader
    uploader = new FileUploader({
      url: ConstantService.HOST + '/upload', // --string
      method: 'POST', // --string
      itemAlias: 'uri', // --string
      authToken: token, // authtoken--string
      authTokenHeader: 'Authorization', // authtoken--string
      headers: [{name: 'accept', value: 'application/json'}], // --Array<headers>
      removeAfterUpload: true, // --boolean
      maxFileSize: 5 * 1024 * 1024 // --number
    })
    uploader.onBeforeUploadItem = (item) => {
      item.withCredentials = false
    }
    return uploader
  }

  // static formatBytes(bytes, decimals?) {
  //   if (bytes === 0) { return '0 Bytes'}
  //   const k = 1024,
  //     dm = decimals || 2,
  //     sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
  //     i = Math.floor(Math.log(bytes) / Math.log(k))
  //   return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i]
  // }

  /**
   * @desc 
   * */
  isValidFile(file) {
    const reg = new RegExp(/\.(jpe?g|png|gif|bmp|svg)$/i)
    const formatMatch = reg.test(file.name)
    const maxSize = UploaderService.MAX_FILE_SIZE
    if (!formatMatch || file.size > maxSize) {
      let message = '', title = ''
      if (!formatMatch) {
        title = `${file.name}`
        message = 'jpgjpegpnggifbmpsvg'
      } else {
        title = `${file.name}`
        message = `${file.name}${UploaderService.formatBytes(file.size)},
        ${UploaderService.formatBytes(maxSize)}`
      }
      appAlert.common.confirmWarning(title, message)
      return false
    }
    return true
  }
}
